        "pink": "I looked around the atrium, at the art and furniture admiring it. I went out into the garden and got a bit wet and muddy. I looked at the graves as I am a keen historian. I then went to the kitchen to see the Admiral. He had made a list of all the guests. I went upstairs and used the bathroom and put on some makeup. I went back downstairs and discovered that there was a secret passage. After this I went to see the Admiral again and give Goldie a treat, and that's when the power cut happened. ",
        "black": "First I took a look around the house to work out where everything was. Then I needed a wee so I went to the bathroom. I went into the garden to have a look at the plants, but it was raining and I got muddy so I went back inside to get an umbrella and clean myself off. I spent the rest of the evening looking at the art and reading books, apart from a brief visit to the kitchen to look at what was cooking. I passed the admiral a few times but we didn't interact much.",
        "green": "I started the evening in the study, having a nice read of the Admiral's literature. I tried exploring the house, and visited most of the rooms at one point or another, sitting down for a rest in many of them. However, I was overcome with sickness, and had to take a step outside for a while. I was in the porch, sat on the bench, when the lights went out and I heard Goldie barking. By the time I got to the Admiral, he had no pulse and no signs of life.",
